@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Work Sans', sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;background-color:#0a0e11;height:100vh;overflow:hidden}.App-header{display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:3em;color:white;height:40%}.App-main{display:flex;width:100%;align-items:center;justify-content:center}.game{display:table;border-spacing:20px}.row{display:table-row}.cell{width:100px;height:100px;background:#eee;border-radius:10%;display:table-cell;box-shadow:-10px -10px 30px 4px rgba(0,0,0,0.1),10px 10px 30px 4px rgba(45,78,255,0.15)}

